.about-me-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 50px 0 0 0;
}
.about-me-parent {
  /*max-width: 1300px;*/
  width: 90%;
}
.about-me-card {
  width: 100%;
  border-radius: 14px;
  box-shadow: 0 0px 20px -2px #00275A;
  

}
.about-me-details {
  width: 100%;
  text-align: justify;
  padding: 40px;
  align-items:normal;
}
.about-me-description {
  font-size: 14px;
  line-height: 250%;
  font-size: 1.1rem;
  /*text-align: center;*/
  justify-content: center;

}
.about-me-highlights {
  margin: 70px 0;
}
.highlight-heading {
  font-family: "Poppins Bold";
  font-size: 14px;
  margin: 0 0 5px 0;
}
.highlight {
  display: flex;
  align-items: center;
  margin: 5px 0;
  font-size: 12px;
  line-height: 250%
}
.highlight-blob {
  height: 12px;
  width: 12px;
  margin: 0 10px 0 0;
  background-color: #00275A;
  border-radius: 50%;
}
  
.description-paragraphe1,  
.description-paragraphe2, 
.description-paragraphe3, 
.description-paragraphe4{
  margin: 2%;
}


  /*IPAD PRO RESPONSIVENESS*/
  @media only screen and (max-width: 1110px) {
    .about-me-parent {
      width: 88%;
    }
  }
  @media only screen and (max-width: 898px) {
    .about-me-profile {
      display: none;
    }
    .about-me-details {
      width: 100%;
    }
  }
  @media only screen and (max-width: 466px) {
    .about-me-options {
      display: flex;
      flex-direction: column;
    }
    .about-me-options .highlighted-btn {
      margin: 24px 0 0 0 !important;
    }
    .about-me-options button {
      width: 100%;
    }
  }

  
@media (min-width: 1980px){
  .about-me-details {
    width: 100%;
    text-align:center;
    padding: 40px;
   
  }
  
  
}
  