.main-container {
    /* background-image: url("../../assets/ContactMe/contact_us.jpeg"); */
    margin: 100px 0 100px 0;
    padding: 50px 0 50px 0;
    background-color: #00275A;
}

.central-form {
    display: flex;
    flex-direction: column;
    /*margin: 0 auto;
    justify-content: center;
    align-items: center;*/
}
 
.central-form h2 {
    color: #fff;
    font-family: "Poppins Bold";
    letter-spacing: 0.2rem;
    display: flex;
    justify-content: center;
    padding-bottom: 4%;
    
  }

/*Form*/
.back-form {
    width: 50%;
    margin: 0 auto;
    /*justify-content: center;*/
    align-items: center;
}

.central-form form {
    width: auto;
    box-shadow: 0 0 2px 1px white;
    display: flex;
    padding: 15px;
    justify-content: center;
    flex-direction: column;
    margin-top: -52px;
    opacity: 0.9;
    background-color: white;
    box-shadow: 0 0px 20px -2px #a8cbf8;
    border-radius: 12px;
}
form p {
    text-align: center;
    margin-bottom: 10px;
    color: #00275A;
    letter-spacing: 0.1rem;
    font-family: "Poppins Bold";
  }
form label {
    margin-bottom: 5px;
    color: #00275A;
    letter-spacing: 0.2rem;
  }
form input,
textarea {
    width: 100%;
    border: 2px solid rgba(0, 0, 0, 0);
    outline: none;
    background-color: rgba(230, 230, 230, 0.6);
    padding: 0.5rem 1rem;
    font-size: 1.1rem;
    margin-bottom: 22px;
    transition: 0.3s;
    border-radius: 10px;
  }
textarea:focus {
    border: 2px solid rgba(30, 85, 250, 0.47);
    background-color: #fff;
  }
form input:focus {
    border: 2px solid rgba(30, 85, 250, 0.47);
    background-color: #fff;
  }
form button {
    margin-top: 15px;
    padding: 5px;
    outline: none;
    width: 900px;
    color: rgb(230, 227, 227);
    border: 2px solid #4191f9;
    font-size: 12px;
    border-radius: 19px;
    background-color: #00275A;
    font-size: 18px;
    width: 160px;
    align-items: center;
    justify-content: center;
}
form button:hover {
    background-color: #1f2235;
    transition: all ease 1s;
    border: 2px solid #1f2235;
}
button .load {
    width: 100%;
}
.col i {
    padding: 7px;
    color: #fff;
    text-align: center;
}
.col i:hover {
    transform: scale(1.5);
}

@media screen and (max-width: 1500px) {
  .back-form {
      display: flex;
      justify-content: space-between;
      margin-top: 30px;
  }
  .central-form h2 {
    letter-spacing: 0.2rem;
    margin-bottom: 8%;
  }
  .back-form {
    width: 90%;
    /*margin: 0 auto;*/
}
  form {
      flex: 0.9;
  }

}

@media screen and (max-width: 600px) {
  .central-form h2 {
      font-size: 28px;
      padding-bottom: 10%;

  }
  .back-form {
    width: 95%;
    margin: 0 auto;
    justify-content: center;
    align-items: center;

}
  
}
  